#alert-layout {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #311D;
  display: flex;
  color: white;
}
#alert-title {
  font-size: 2em;
  text-align: center;
}
#alert-layout > div {
  margin: auto;
  display: flex;
  flex-direction: column;
}
#alert-layout button {
  box-shadow: none;
  margin: 4px 2px;
}

/* title */
h1 {
  text-align: center;
}

/* timer */
#timer-layout {
  margin: auto auto 50% auto;
  text-align: center;
}
#curr-time,
input.time-number {
  font-size: 4rem;
  font-family: monospace;
}
#curr-time * {
  display: inline-block;
}
.time-number {
  padding: 0;
  box-sizing: border-box;
  border: none;
}
.time-number,
input.time-number {
  width: 1.2em;
  text-align: center;
}
input.time-number {
  opacity: 0.5;
}
/* hide spinner button in input */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* for firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* editing notification */
#noti-editing {
  margin: 0 2px;
  text-align: center;
}

/* button */
button {
  color: #888;
  font-size: 1.2em;
  font-weight: bold;
  padding: 4px 6px;
  border: 1px solid #888;
  border-radius: 6px;
  background-color: white;
  box-shadow: 0 2px 2px #DDD;
}
button:active {
  background-color: #888;
  color: white;
  box-shadow: 0 1px 2px #AAA;
}
button.emphasis {
  background-color: #FA0;
  border: 1px solid #FA0;
  color: white;
}
button.emphasis:active {
  background-color: white;
  color: #FA0;
}
</style>
